.blog-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.blog-intro {
  text-align: center;
  font-size: 1.2rem;
  color: var(--secondary-color);
  margin-bottom: 3rem;
}

.blog-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
  margin-top: 2rem;
}

.blog-card {
  background: white;
  border-radius: 12px;
  overflow: hidden;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease;
}

.blog-card:hover {
  transform: translateY(-5px);
}

.blog-image {
  position: relative;
  height: 200px;
  overflow: hidden;
}

.blog-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform 0.3s ease;
}

.blog-card:hover .blog-image img {
  transform: scale(1.05);
}

.category-tag {
  position: absolute;
  top: 1rem;
  left: 1rem;
  background: var(--primary-color);
  color: white;
  padding: 0.5rem 1rem;
  border-radius: 20px;
  font-size: 0.8rem;
  font-weight: 500;
}

.blog-content {
  padding: 1.5rem;
}

.blog-meta {
  display: flex;
  justify-content: space-between;
  color: var(--secondary-color);
  font-size: 0.9rem;
  margin-bottom: 1rem;
}

.blog-content h2 {
  font-size: 1.4rem;
  margin-bottom: 1rem;
  color: var(--dark-text);
  line-height: 1.4;
}

.blog-content p {
  color: var(--secondary-color);
  line-height: 1.6;
  margin-bottom: 1.5rem;
}

.read-more {
  color: var(--primary-color);
  text-decoration: none;
  font-weight: 500;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  transition: gap 0.3s ease;
}

.read-more:hover {
  gap: 0.8rem;
}

.read-more i {
  font-size: 0.9rem;
}

/* Dark theme styles */
body.dark-theme .blog-card {
  background: #1e1e1e;
}

body.dark-theme .blog-content h2 {
  color: var(--light-text);
}

body.dark-theme .blog-content p {
  color: #a0a0a0;
}

body.dark-theme .blog-meta {
  color: #888;
}

@media (max-width: 768px) {
  .blog-grid {
    grid-template-columns: 1fr;
  }
  
  .blog-container {
    padding: 1rem;
  }
  
  .blog-intro {
    font-size: 1.1rem;
    margin-bottom: 2rem;
  }
} 